import React, { FC } from 'react';
import { useForm } from 'react-hook-form';
import { RouteComponentProps } from 'react-router';
import { Card, CardBody, CardTitle, Col, CustomInput, Row } from 'reactstrap';
import { EditForm, EditViewProps, getSelectedItem, InputGroupInline, InputType, onlyNumber, WindowComponent } from 'summer';
import { CLASS_NAME, ConfigCsll } from '../../../../models/dtos/mlf/configCsll';
import { DIAS_ENUM_VALUES } from '../../../../models/enumerated/ger/diasEnum';
import { DEBITO_SCP_INC_ENUM_VALUES } from '../../../../models/enumerated/mlf/debitoSCPINCEnum';
import { PERIODO_APURACAO_ENUM_VALUES } from '../../../../models/enumerated/mlf/periodoApuracaoEnum';
import { TIPO_APURACAO_ENUM_VALUES } from '../../../../models/enumerated/mlf/tipoApuracaoEnum';

interface ConfigCsllEditViewProps extends EditViewProps<ConfigCsll> {}

const ConfigCsllEditView: FC<ConfigCsllEditViewProps & RouteComponentProps> = props => {
  const selectedItem = getSelectedItem(props);
  const formProps = useForm<ConfigCsll>({
    defaultValues: selectedItem,
    mode: 'onBlur',
  });
  const validateExtras = formValues => {
    let mensagens = [];
    if (formValues.notificarVencimento === true && !formValues.notificarVencimentoDias) {
      mensagens = [...mensagens, 'Dias de Antecedência do vencimento - Campo não pode estar em branco.'];
    }
    if (formValues.adicionalVrLimite > 0 && formValues.adicionalAliquota === 0) {
      mensagens = [...mensagens, 'Alíquota adicional não pode estar em branco quando existir valor limite.'];
    }
    return mensagens;
  };

  const esconderBotaoSubmit = () => {
    const padrao = formProps.getValues().padrao || false;
    return padrao.toString() === 'true';
  };

  return (
    <WindowComponent title="Configuração do CSLL">
      <EditForm
        formProps={formProps}
        editViewProps={props}
        dtoClassName={CLASS_NAME}
        validateExtras={validateExtras}
        hideSubmitButton={esconderBotaoSubmit()}
      >
        <InputGroupInline formProps={formProps} id="padrao" hidden={true} />
        <Row>
          <Col>
            <InputGroupInline
              id="descricao"
              label="Descrição"
              formProps={{ ...formProps, validation: { required: true, maxLength: 50 } }}
              isPrependLabel={true}
              placeholder="Informe a descrição da Configuração de CSLL"
            />
          </Col>
        </Row>
        <Row>
          <Col>
            <InputGroupInline
              id="codigoReceita"
              label="Código da Receita"
              formProps={{ ...formProps, validation: { required: true, maxLength: 5 } }}
              isPrependLabel={true}
              placeholder="Informe o código da receita"
            />
          </Col>
        </Row>
        <Row>
          <Col>
            <InputGroupInline
              id="variacaoCodigoReceita"
              label="Variação"
              formProps={{ ...formProps, validation: onlyNumber(0, 2) }}
              tooltipProps={{ message: 'Variação do código da receita. Esse campo é obrigatório caso a empresa gere o DCTF.' }}
              isPrependLabel={true}
              placeholder="Informe a variação da Receita"
            />
          </Col>
        </Row>
        <Row>
          <Col>
            <InputGroupInline
              id="periodoApuracao"
              label="Período de Apuração"
              formProps={{ ...formProps, validation: { required: true } }}
              isPrependLabel={true}
              placeholder="Informe o período de apuração"
              inputDropDownProps={{ optionsProvider: PERIODO_APURACAO_ENUM_VALUES }}
              type={InputType.DROP_DOWN}
            />
          </Col>
        </Row>
        <Row>
          <Col>
            <InputGroupInline
              id="diaVencimento"
              label="Dia de Vencimento"
              formProps={{ ...formProps, validation: { required: true } }}
              isPrependLabel={true}
              placeholder="Informe o dia de vencimento"
              inputDropDownProps={{ optionsProvider: DIAS_ENUM_VALUES }}
              type={InputType.DROP_DOWN}
            />
          </Col>
        </Row>
        <Row>
          <Col>
            <Card style={{ marginBottom: '16px' }}>
              <CardBody>
                <CardTitle>Configuração de Notificação</CardTitle>
                <Row>
                  <Col>
                    <CustomInput
                      type="switch"
                      label="Notificar Vencimento de Imposto"
                      name="notificarVencimento"
                      id="checkNotificarVencimento"
                      innerRef={formProps.register}
                      style={{ marginBottom: '16px' }}
                    />
                  </Col>
                </Row>
                <Row>
                  <Col>
                    <InputGroupInline
                      id="notificarVencimentoDias"
                      label="Dias de Antecedência do Vencimento"
                      formProps={formProps}
                      isPrependLabel={true}
                      placeholder="Informe com quantos de dias de antecedência gostaria de ser notificado"
                      disabled={!formProps.getValues().notificarVencimento}
                      inputDropDownProps={{ optionsProvider: DIAS_ENUM_VALUES, enabled: false }}
                      type={InputType.DROP_DOWN}
                    />
                  </Col>
                </Row>
                <Row>
                  <Col>
                    <CustomInput
                      type="switch"
                      disabled={!formProps.getValues().notificarVencimento}
                      label="Notificar o cliente/responsável por Connect/email, sobre o vencimento do Imposto"
                      name="notificarVencimentoPorEmail"
                      id="checkNotificarVencimentoPorEmail"
                      innerRef={formProps.register}
                    />
                  </Col>
                </Row>
              </CardBody>
            </Card>
          </Col>
        </Row>
        <Row>
          <Col>
            <InputGroupInline
              id="aliquota"
              label="Alíquota"
              formProps={{ ...formProps, validation: { required: true, minValue: 0, maxValue: 100 } }}
              isPrependLabel={true}
              placeholder="Informe o valor do alíquota"
              type={InputType.NUMBER}
            />
          </Col>
        </Row>
        <Row>
          <Col>
            <InputGroupInline
              id="aliquotaBaseVenda"
              label="Alíquota Base Venda"
              formProps={{ ...formProps, validation: { required: true, minValue: 0, maxValue: 100 } }}
              isPrependLabel={true}
              placeholder="Informe o valor da alíquota base de venda"
              type={InputType.NUMBER}
            />
          </Col>
        </Row>
        <Row>
          <Col>
            <InputGroupInline
              id="aliquotaBasePrestServ"
              label="Alíquota Base Prest. Serv."
              formProps={{ ...formProps, validation: { required: true, minValue: 0, maxValue: 100 } }}
              isPrependLabel={true}
              placeholder="Informe o valor da alíquota base da prestação de serviço"
              type={InputType.NUMBER}
            />
          </Col>
        </Row>
        <Row>
          <Col>
            <InputGroupInline
              id="aliquotaBaseTranspCarga"
              label="Alíquota Base Transp. Carga"
              formProps={{ ...formProps, validation: { required: true, minValue: 0, maxValue: 100 } }}
              isPrependLabel={true}
              placeholder="Informe o valor da alíquota base do transporte de carga"
              type={InputType.NUMBER}
            />
          </Col>
        </Row>
        <Row>
          <Col>
            <InputGroupInline
              id="aliquotaMaterialEmpregado"
              label="Alíquota Mat. Empregado"
              formProps={{ ...formProps, validation: { required: true, minValue: 0, maxValue: 100 } }}
              isPrependLabel={true}
              placeholder="Informe o valor da alíquota do material empregado"
              type={InputType.NUMBER}
            />
          </Col>
        </Row>
        <Row>
          <Col>
            <InputGroupInline
              id="recolhimentoMinimo"
              label="Recolhimento Mínimo"
              formProps={{ ...formProps, validation: { required: true } }}
              isPrependLabel={true}
              placeholder="Informe o valor do recolhimento mínimo"
              type={InputType.NUMBER}
            />
          </Col>
        </Row>
        <Row>
          <Col>
            <InputGroupInline
              id="tipoApuracao"
              label="Tipo de Apuração"
              formProps={{ ...formProps, validation: { required: true } }}
              isPrependLabel={true}
              placeholder="Informe o tipo de apuração"
              inputDropDownProps={{ optionsProvider: TIPO_APURACAO_ENUM_VALUES, enabled: false }}
              type={InputType.DROP_DOWN}
            />
          </Col>
        </Row>
        <Row>
          <Col>
            <InputGroupInline
              id="limiteCompensacao"
              label="Limite de Compensação"
              formProps={{ ...formProps, validation: { required: true } }}
              isPrependLabel={true}
              placeholder="Informe o Limite de Compensação"
              type={InputType.NUMBER}
            />
          </Col>
        </Row>
        <Row>
          <Col>
            <Card style={{ marginBottom: '16px' }}>
              <CardBody>
                <CardTitle>Configuração para o DCTF</CardTitle>
                <Row>
                  <Col>
                    <InputGroupInline
                      id="debitoSCPINC"
                      label="Débito SCP/INC"
                      formProps={formProps}
                      isPrependLabel={true}
                      placeholder="Informe o Débito SCP/INC"
                      inputDropDownProps={{ optionsProvider: DEBITO_SCP_INC_ENUM_VALUES, enabled: false }}
                      type={InputType.DROP_DOWN}
                    />
                  </Col>
                </Row>
              </CardBody>
            </Card>
          </Col>
        </Row>
        <Row>
          <Col>
            <Card style={{ marginBottom: '16px' }}>
              <CardBody>
                <CardTitle>Adicional para o Imposto</CardTitle>
                <Row>
                  <Col>
                    <InputGroupInline
                      id="adicionalVrLimite"
                      label="Valor Limite"
                      formProps={{ ...formProps, validation: { required: true } }}
                      isPrependLabel={true}
                      placeholder="Informe o valor limite"
                      type={InputType.NUMBER}
                    />
                  </Col>
                </Row>
                <Row>
                  <Col>
                    <InputGroupInline
                      id="adicionalAliquota"
                      label="Alíquota"
                      formProps={{ ...formProps, validation: { required: true, minValue: 0, maxValue: 100 } }}
                      isPrependLabel={true}
                      placeholder="Informe a alíquota"
                      type={InputType.NUMBER}
                    />
                  </Col>
                </Row>
              </CardBody>
            </Card>
          </Col>
        </Row>
      </EditForm>
    </WindowComponent>
  );
};

export default ConfigCsllEditView;
